import React, {useState, useEffect} from 'react';
import './utils/ResponsePage.css';
import {dbService} from '../tools/fbase';
import ReactGa from 'react-ga'
import AskPassword from './utils/AskPassword'
import NavBarV2 from '../NavBar/NavBarV2'
import Footer from '../NavBar/Footer'
import { Table, Tag, Space } from 'antd';
import gadata from './data/gadata';

const columns = [
    {
      title: '',
      dataIndex: 'email',
      key: 'email',
      render: text => <a>{text}</a>,
    },
    {
      title: ' ',
      dataIndex: 'date',
      key: 'date',
      render: date => <span>2021 {date.getMonth()+1} {date.getDate()} {date.getHours()}  </span>
    },
  ];
  
function ResponsePage() {
    const [id, setId] = useState("");
    const [password, setPassword] = useState("");
    const [loading, setLoading] = useState(false);
    const [get, setGet] = useState(false);
    const [reponses, setReponses] = useState([{
        email:"",
        created:0,
        date:"now"
    }]);
    const [part, setPart] = useState(1);
    const [myDatas, setMyDatas] = useState({
        all_sessions:0,
        all_pageViews:0
    });
    
    useEffect(() => {
        ReactGa.initialize('UA-213792742-1');
        // to report page view
        ReactGa.pageview('/seeResponse');
        getDatas();
    },[loading])

    const getDatas = async () => {
        const reDatas = await dbService
        .collection("apply-datas")
        .orderBy("created", "desc")
        .get(); // uid creatorId .

        let reData = reDatas.docs.map(doc => {
            let day = new Date(doc.data().created)
            return({...doc.data(), id:doc.id, date:day})
        });
        setReponses(reData);

        // let p = [];
        // for(var i = 0; i<Math.ceil(reData.length/10); i++){
        //     p.push(i+1);
        // }
        // setPages(p);

        setLoading(true);
    }

    const getMyData = (path) => {
        let all_data = gadata.reports[0].data.rows;
        
        const myData = all_data.filter(item => item.dimensions[2] === `${path}`);
        let all_sessions = 0;
        let all_pageViews = 0;
        myData.map((item, index) => {
            console.log("", item.dimensions);
            console.log("  : ", item.metrics[0].values[0]);
            console.log("   : ", item.metrics[0].values[1]);
            console.log("  : ", item.metrics[0].values[2]);
            all_sessions += parseInt(item.metrics[0].values[0]);
            all_pageViews += parseInt(item.metrics[0].values[1]);
        })
        console.log("   : ", all_sessions );
        console.log("    : ", all_pageViews );
        const body = {
            all_sessions,
            all_pageViews,
        }
        setMyDatas(body);
    }

    const getByPassWord = () => {
        if(password === "surfee.ourdata"){
            const path = '/'
            setGet(!get);
            getDatas();
            getMyData(path)
        }else{
            // password firebase where     .
            //     
            //      
            //    path( urlId )  GA  
            alert("   !");
        }
    }

    if(get === false){
        return(
            <AskPassword password={password} setPassword={setPassword} getByPassWord={getByPassWord}/>
        )
    }else{
        if(loading === true){
            return (
                <div style={{backgroundColor:'white', display:'flex', justifyContent:'center',alignItems:'center', width:'100%', flexDirection:'column'}}>
                <NavBarV2 />
                <div className="get-all-container">
                    <div className="get-up-container">
                        <div className="get-up-title">
                              
                        </div>
                        <div className="get-buttons-container">
                            <button className="get-part-button" style={{backgroundColor:`${part === 1 ? "#6a63f76e" : "white"}`}} onClick={e => setPart(1)}></button>
                            <button className="get-part-button" style={{backgroundColor:`${part === 2 ? "#6a63f76e" : "white"}`}} onClick={e => setPart(2)}></button>
                        </div>
                    </div>
                    <div className="get-down-container">
                        {  part === 1 ? 
                        <div className="response-container">
                            <div className="response-table">
                                <div className="response-table-top">
                                    <span className="response-table-title">    : {reponses.length}  </span>
                                </div>
                                <Table columns={columns} dataSource={reponses} className="response-table-datas"/>
                            </div>
                        </div>
                        :
                        <>
                        <div style={{height:'100vh'}}>
                            <button onClick={() => getMyData()}> </button>
                            <div>
                                <span className="data-one-mini-card">    {myDatas.all_pageViews}</span>
                                <span className="data-one-mini-card">   {myDatas.all_sessions}</span>
                            </div>
                        </div>
                        </>
                        }
                    </div>
                </div>
                <Footer />
                </div>
            )
        }else{
            return(
                <></>
            )
        }
    }
}

export default ResponsePage
